header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: calc(33.33vh / 3); 
    background-color: #f5f5f5;
    box-sizing: border-box;
    padding: 10px;
    border: 2px solid #333;
    border-bottom: none;
    margin-top: 5px;
    margin-left:15px;
    margin-right:15px;
  
  }
  
  header .links a {
    color: #333;
    text-decoration: none;
    margin: 0 50px;
    position: relative;
  } 
  
  header .links .active::after {
    content:"";
    position: absolute;
    bottom: -15px; 
    left: -35px; 
    width: 130px;
    height: 3px;
    background-color:rgb(120, 221, 221);

  }
  
   
  header .icon-left img,
  header .icon-right img {
    width: 24px;
    height: 24px;
  }
  
  
  header .icon-right a {
    text-decoration: none;
  }