.container2 {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    flex-grow: 1;
    width: 100%;
  }

  .row1 {
    display: flex;
    flex-direction:row;
    justify-content:space-around;
    align-items: center;
    height:70vh;
    box-sizing: border-box;
    border: 2px solid #333;
    margin-left: 15px;
    margin-right:15px;
    padding-left: 100px;
    padding-right:100px;
  }

  .row2 {
    display: flex;
    flex-direction:row;
    align-items: stretch;
    height:90vh;
    box-sizing: border-box;
    border: 2px solid #333;
    border-top: none;
    margin-left: 15px;
    margin-right:15px;
  }

  .row2 img{
    display:flex;
    flex-basis: 50%;
    box-sizing: border-box;
    padding-left: 100px;
    padding-right:50px;
  }

  .row2right{
    display:flex;
    flex-basis: 50%;
    flex-direction: column;
    justify-content: top;
    align-items: left;
    padding-top: 30px;
    padding-left:100px;
    box-sizing: border-box;
  }

  .producticon{
    cursor:pointer;
  }
