.block {
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-grow: 1;
    height: auto;
    min-height: 80vh;
    box-sizing: border-box;
    border: 2px solid #333;
    padding-top: 30px;
    margin-left: 15px;
    margin-right:15px;
  }

.accordion{
    display:flex;
    justify-content: space-around;
    flex-direction: column;
    align-items: center;
    width:fit-content;
    height: auto;
    
}

.title{
   display: flex;
   justify-content: center;
   width: 600px;
   padding-top: 10px;
   padding-bottom: 10px;
   background-color: dimgray;
   cursor: pointer;
}

.title.active{
    display: flex;
    justify-content: center;
    width: 600px;
    padding-top: 10px;
    padding-bottom: 10px;
    background-color: dimgray;
    cursor: pointer;
 }

 .accordion-content.active{
    display: flex;
    justify-content: center;
    width: 600px;
    padding-top: 30px;
    padding-bottom: 30px;
    margin-bottom: 15px;
    background-color: rgb(233, 230, 230);
 }

 .jar{
    display: flex;
    flex-direction: column;
    margin-bottom: 40px;
 }

 .topic{
   margin-right: 230px;
   margin-left: 250px;
 }

 .arrow{
   border: solid black;
   border-width: 0 3px 3px 0;
   display: inline-block;
   padding: 9px;
   transform: rotate(45deg);
   -webkit-transform: rotate(45deg);
 }

 .arrow.active{
   border: solid black;
   border-width: 0 3px 3px 0;
   display: inline-block;
   padding: 9px;
   transform: rotate(-135deg);
   -webkit-transform: rotate(-135deg);
 }


