.goods-container1 {

    display: flex;
    flex-direction: column;
    align-items: center;
    flex-grow: 1;
    height: auto;
    min-height: 100vh;
    box-sizing: border-box;
    border: 2px solid #333;
    padding-top: 30px;
    padding-bottom: 180px;
    margin-left: 15px;
    margin-right:15px;

  }


  .goods-container2 {

    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: stretch;

  }

  .goods-container2 div {
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-basis: 33%;  
    box-sizing: border-box; 
    margin-top: 60px;
  }


