.goodsDetail-container {

    display: flex;
    flex-direction: column;
    align-items: center;
    flex-grow: 1;
    height: auto;
    min-height: 90vh;
    box-sizing: border-box;
    border: 2px solid #333;
    padding-top: 30px;
    padding-bottom: 50px;
    margin-left: 15px;
    margin-right:15px;

  }

.inputform{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    width: 800px;
    height: 500px;
}

.PriceInput{
    font-size: medium;
    width: 800px;
    height:50px;
}

.PriceInput::placeholder{
    font-size: medium;
    font-weight: bold;
}

.DescriptionInput{
    font-size: medium;
    width:800px;
    height:300px;
}

.DescriptionInput::placeholder{
    font-size: medium;
    font-weight: bold;
}

button{
    width: 400px;
    height: 50px;
    background-color: rgb(7, 190, 190);
    color:white;
    cursor: pointer;
}

.infoshow{
   display: flex;
   align-items: center;
   width: 1200px;
}

.infoshowp{
   display: flex;
   font-size:2em;
   font-weight: bold;
}

.infoshowwords{
  display: flex;
  flex-direction: column;
  align-items:flex-start;
  height: 300px;
  margin-left: 230px;
  justify-content: space-around;
}

.infoshowhead{
    font-size:4em ;
    font-weight: bold;
}