.jar2 {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    flex-grow: 1;
    width: 100%;
  }

  .part1 {
    display: flex;
    flex-direction:column;
    justify-content:space-around;
    align-items: stretch;
    height:80vh;
    box-sizing: border-box;
    border: 2px solid #333;
    margin-left: 15px;
    margin-right:15px;
    padding-left: 100px;
    padding-right:100px;
    padding-bottom: 60px;
    padding-top:40px;

  }

  .part2 {
    display: flex;
    flex-direction:row;
    justify-content: center;
    height:30vh;
    box-sizing: border-box;
    border: 2px solid #333;
    border-top: none;
    margin-left: 15px;
    margin-right:15px;
    padding-top:30px;
    padding-bottom: 60px;
  }

  .innerbutton{
    display: flex;
    justify-content: center;
  }

  .button1{
    width: 260px;
    height: 50px;
    background-color: rgb(81, 146, 226);
    color: aliceblue;
    cursor: pointer;
  }

  .button2{
    width: 180px;
    height: 90px;
    background-color: rgb(81, 146, 226);
    color: aliceblue;
    cursor: pointer;
  }

  .content{
    display: flex;
    align-items: stretch;
    justify-content: space-evenly;
    flex-basis: 60%;
  }

  .contentleft{
    display: flex;
    flex-direction: column;
  
    align-items: center;
    justify-content:space-between;  
  }

  .contentright{
    display:flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
  }

  h2{
    display:flex;
    justify-content: center;
  }

  .inputcontent{
width:400px;
height: 40px;

  }

  .gap{
   width: 400px;
   height:140px;
   resize: none;
  }