.container {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    flex-grow: 1;
    /* height: 33.33vh; */
    height: 200vh;
    width: 100%;
    /* overflow: auto; */
    /* padding: 20px; */
  }
  
  .row {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-basis: 33.33%; 
    flex-grow: 1; 
    /* margin-bottom: 20px; */
    box-sizing: border-box;
    border: 2px solid #333;
    border-bottom: none;
    margin-left: 15px;
    margin-right:15px;
    padding: 70px;
  }
  
  .text-row{
    display: flex;
    flex-direction: column;
    align-items:flex-start;
    justify-content: center;
    box-sizing: border-box;
  }
  
  .row:last-child{
    border-bottom: 2px solid #333;
  }
  
  .two-columns-row {
    align-items:stretch;
    flex-direction: row;
    flex-basis: 33.33%;
    padding: 0%; 
  }
  
  .left,
  .right {
    flex-basis: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
  }
  
  .right {
    flex-direction: column;
    align-items: flex-start;
    /* padding:70px; */
  }
  
  .left {
    border-right: 2px solid #333;
  }
  
  .text-row p,
  .right p {
    font-size: 1.2rem;
    color: #333;
    margin-right: 20%;
    margin-left:10%
  }
  
  .text-row h1{
    margin-right: 20%;
    margin-left:10%
  }
  
  .right h1{
    margin-right: 20%;
    margin-left:10%
  }
  
  .image-row {
    padding:0%;
  } 
  
  .image-row img {
    max-width: 80%;
  }