footer,.containerBottom {
    display: flex;
    justify-content: center;
    align-items: center;
    /* height: calc(33.33vh / 3); the same height as a single row */
    height: 60vh; 
    /* height:360px; */
    background-color: #f5f5f5;
    box-sizing: border-box;
    border: 2px solid #333;
    border-top: none;
    margin-bottom: 5px;
    margin-left:15px;
    margin-right:15px;
  }
  
  .rightBox {
    flex-basis: 50%;
    display: flex;
    flex-direction: column;
    align-items: stretch;
    justify-content: center;
    box-sizing: border-box;
  }
  
  .rightBox .icon {
    display:flex;
    flex-direction: row;
    /* margin: 0 25px; */
    justify-content: space-between; 
    padding-left:150px;  
    padding-right: 150px;
  }
  
  .rightBox .icon img{
    height:80%;
  }
  
  .rightBox h1 {
    display:flex;
    align-items: flex-start;
    padding-left: 150px; 
  }
  
  .leftBox {
    flex-basis: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    box-sizing: border-box;
    border-right: 2px solid #333;
    /* padding: 70px; */
  }
  
  .leftBox p {
    width:60%;
    font-size: 1.2rem;
    color: #333;
    text-decoration: underline;
    margin-right: 20%;
    margin-left:10%
  }
  
  .leftBox h1{
    margin-right: 20%;
    margin-left:10%
  }